<template>
    <div class="nav-item d-inline">
        <div class="nav-link" v-b-modal.sign-up>
          <p class="text-white">signup</p>
        </div>

        <b-modal
        id="sign-up"
        ref="modal"
        title=""
        @show="resetModal"
        @hidden="resetModal"
        hide-footer
        >
        <form ref="form" @submit.stop.prevent="handleSubmit">
            <b-form-group
                label="ID"
                label-for="uid-input"
            >
            <b-form-input
                id="uid-input"
                v-model="userData.uid"
                required
                placeholder="Enter ID"
            ></b-form-input>
            </b-form-group>

            <b-form-group
                label="NICKNAME"
                label-for="name-input"
            >
            <b-form-input
                id="name-input"
                v-model="userData.name"
                required
                placeholder="Enter NICKNAME"
            ></b-form-input>
            </b-form-group>

            <b-form-group
                label="PASSWORD"
                label-for="password-input"
            >
            <b-form-input
                type="password"
                id="password-input"
                v-model="password1"
                required
                placeholder="Enter Password"
            ></b-form-input>
            </b-form-group>

            <b-form-group
                label="CONFIRM PASSWORD"
                label-for="password-input"
            >
            <b-form-input
                type="password"
                id="password-input"
                v-model="password2"
                required
                placeholder="Check Password"
            ></b-form-input>
            </b-form-group>

            <b-form-group
              id="input-group-1"
              label="Email"
              label-for="email-input"
              description="  ."
            >
              <b-form-input
                id="email-input"
                v-model="userData.email"
                type="email"
                required
                placeholder="Enter email"
              ></b-form-input>
            </b-form-group>

            

            <b-form-group id="input-group-3" label="1" label-for="inter1-input">
              <b-form-select
                id="inter1-input"
                v-model="userData.interest1"
                :options="interests"
                required
              ></b-form-select>
            </b-form-group>

            <b-form-group id="input-group-3" label="2" label-for="inter1-input" description=" 1  ">
              <b-form-select
                id="inter1-input"
                v-model="userData.interest2"
                :options="interests"
                required
              ></b-form-select>
            </b-form-group>

            <b-button class="mt-3" block @click=handleSubmit>Signup</b-button>
        </form>
        </b-modal>
    </div>

</template>

<script>
import axios from 'axios'
import { sync } from 'vuex-pathify'

const API_URL = 'http://localhost:8080/'

export default {
    name: 'Signup',
    data() {
        return {
            userData: {
              email: '',
              uid: '',
              password: '',
              name: '',
              phone: '',
              interest1: '',
              interest2: '',
            },

            password1: '',
            password2: '',

            interests: [
              { text: '   ', value: '' },
              '',
              '',
              'IT',
              '',
              '',
              '',
              '',
              '',
              '',
              '',
            ],
        }
    },

    computed: {
        curUid: sync('curUid')
    },

    methods: {
        resetModal() {
            this.userData.uid = ''
            this.userData.email = ''
            this.password1 = ''
            this.password2 = ''
            this.userData.password = ''
            this.userData.name = ''
            this.userData.phone = ''
            this.userData.interest1 = ''
            this.userData.interest2 = ''
        },
        handleSubmit() {
            // Exit when the form isn't valid
            if (this.userData.uid === '') {
                alert('ID ')
                return
            }
            if (this.userData.email === '') {
              alert('EMAIL  ')
              return
            }
            if (this.userData.name === '') {
              alert('NICKNAME  ')
              return
            }
            if (this.password1 === '' || this.password2 === '') {
                alert('PASSWORD ')
                return
            }
            if (this.password1 !== this.password2) {
              alert('PASSWORD  ')
              return
            }
            if (this.userData.interest1 === '' || this.userData.interest2 === '') {
              alert(' ')
              return
            }
            if (this.userData.interest1 === this.userData.interest2) {
              alert('   ')
              return
            }

            this.signup();
        },

        signup() {
          const url = `${API_URL}account/join`
          this.userData.password = this.password1

          console.log(this.userData)

          console.log(url)
          axios.post(url, this.userData)
          .then(res => {
            console.log(res)
            this.login()
          })
          .catch(() => {
            alert("  ")
            return
          });
        },


        login() {
            const url = `${API_URL}account/login?uid=${this.userData.uid}&password=${this.userData.password}`;
            console.log(url);
            axios
            .get(url)
            .then(({data}) => {
                console.log(data);
                this.curUid = this.userData.uid;
                this.$nextTick(() => {
                this.$bvModal.hide('sign-up')
                })
                console.log('login_success');
                this.$router.push('/')
            })
            .catch(() => {
                alert("ID  PASSWORD ");
                return
            });
        },
    }
}
</script>

